import React, { useRef } from "react";

import emailjs from '@emailjs/browser';

import { motion } from "framer-motion";
import { useState } from "react";

function ContactForm(props) {
  const form = useRef();

  const [firstName, setFirstName] = useState('');
  const [email, setEmail] = useState('');
  const [message, setMessage] = useState('');

  const sendEmail = (e) => {
    e.preventDefault();

    emailjs.sendForm('service_9tjb7cl', 'template_fnnu89c', form.current, 'OW-ZZEozsfHs9fbi_')
      .then((result) => {
          console.log(result.text);
      }, (error) => {
          console.log(error.text);
      });
    setFirstName('');
    setEmail('');
    setMessage('');
    alert('Your letter was sent!')
  };

  return (
    <form ref={form} onSubmit={sendEmail}>
      <input required onChange={event => setFirstName(event.target.value)} value={firstName} className="contact__input" placeholder="Your name" type="text" name="user_name" />
      <input required onChange={event => setEmail(event.target.value)} value={email} className="contact__input" placeholder="Your email" type="email" name="user_email" />
      <textarea required onChange={event => setMessage(event.target.value)} value={message} placeholder="Write a messaage here" name="message" />
      <motion.input whileHover={{scale: 1.1}} whileTap={{scale: 0.9}} className="contact__button" type="submit" value="Send" />
    </form>
  );
};

export default ContactForm